@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: url("https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80");
  background-color: black;
  background-position: 50% 25%;
  background-repeat: no-repeat;
  background-size: cover;
}

.header {
  background-color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px 0px 50px 0px;
}

.header-h1 {
  font-family: "Lobster", cursive;
  font-size: 5.5rem;
  margin-top: 0;
  margin-bottom: 25px;
  color: #dc143c;
  text-shadow: 2px 0px 2px #dc143c;
}

.search {
  width: 280px;
  padding: 8px;
  border: none;
  margin-right: 15px;
  font-size: 1.1rem;
}

.submit {
  padding: 8px 20px;
  border: 1px solid #dc143c;
  background-color: #dc143c;
  color: white;
  font-size: 1.1rem;
  font-weight: bolder;
  cursor: pointer;
  border-radius: 5px;
}

.submit:hover {
  background-color: rgb(180, 22, 54);
  border: 1px solid rgb(180, 22, 54);
}

.movie-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.movie-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  
  .movie {
    position: relative;
    width: 310px;
    background-color: white;
    margin: 1.5rem;
    overflow: hidden;
    border-radius: 6px;
    border: 1px solid white;
    box-shadow: 3px 3px 5px rgba(255, 255, 255, 0.6);
    cursor: pointer;
  }
  
  .movie img {
    width: 100%;
    height: 500px;
  }
  
  .movie-title {
    padding: 5px;
  }
  
  .movie-title h3 {
    margin: 0;
  }
  
  .movie-overview {
    overflow: auto;
    max-height: 100%;
    padding: 5px;
  }
  
  .flip-box {
    background-color: transparent;
    width: 310px;
    height: 500px;
    border: 1px solid white;
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
  
  .flip-box-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: -webkit-transform 1.2s;
    transition: transform 1.2s;
    transition: transform 1.2s, -webkit-transform 1.2s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
  }
  
  .flip-box:hover .flip-box-inner {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
  .flip-box-front,
  .flip-box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .flip-box-back {
    position: relative;
    background-color: rgb(13, 29, 39);
    color: white;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    padding: 15px;
    font-size: 1.2rem;
    text-align: start;
    position: relative;
  }
  
