  }*/
  
  .release_date {
    position: absolute;
    padding: 5px;
    bottom: 0;
    left: 18%;
    text-align: center;
    background-color: #dc143c;
    color: white;
    border-radius: 5px;
  }
  
  .vote_average {
    position: absolute;
    top: 2px;
    right: 2px;
    padding: 10px;
    background-color: #dc143c;
    font-size: 1.4rem;
    font-weight: bolder;
    border-radius: 50%;
  }
  
